import { doc, getDoc, setDoc } from 'firebase/firestore';
import { db } from './firebase';

export interface AppSettings {
  festivalName: string;
  festivalDate: string;
  festivalLocation: string;
  maxVotesPerUser: number;
  allowAnonymousVotes: boolean;
  enableNotifications: boolean;
  maintenanceMode: boolean;
  theme: 'light' | 'dark' | 'auto';
  language: 'fr' | 'en';
  timezone: string;
}

const DEFAULT_SETTINGS: AppSettings = {
  festivalName: 'Festival International de Courts Métrages Francophones de Dinan',
  festivalDate: new Date().toISOString().split('T')[0],
  festivalLocation: 'Dinan, France',
  maxVotesPerUser: 3,
  allowAnonymousVotes: true,
  enableNotifications: true,
  maintenanceMode: false,
  theme: 'light',
  language: 'fr',
  timezone: 'Europe/Paris'
};

/**
 * Charger les paramètres de l'application
 */
export const loadSettings = async (): Promise<AppSettings> => {
  try {
    const settingsDoc = await getDoc(doc(db, 'settings', 'app'));
    
    if (settingsDoc.exists()) {
      const data = settingsDoc.data();
      return {
        ...DEFAULT_SETTINGS,
        ...data
      } as AppSettings;
    } else {
      // Créer les paramètres par défaut
      await saveSettings(DEFAULT_SETTINGS);
      return DEFAULT_SETTINGS;
    }
  } catch (error) {
    console.error('Erreur lors du chargement des paramètres:', error);
    return DEFAULT_SETTINGS;
  }
};

/**
 * Sauvegarder les paramètres de l'application
 */
export const saveSettings = async (settings: AppSettings): Promise<void> => {
  try {
    await setDoc(doc(db, 'settings', 'app'), {
      ...settings,
      updatedAt: new Date()
    });
  } catch (error) {
    console.error('Erreur lors de la sauvegarde des paramètres:', error);
    throw new Error('Impossible de sauvegarder les paramètres');
  }
};

/**
 * Mettre à jour un paramètre spécifique
 */
export const updateSetting = async (key: keyof AppSettings, value: any): Promise<void> => {
  try {
    const currentSettings = await loadSettings();
    const updatedSettings = {
      ...currentSettings,
      [key]: value,
      updatedAt: new Date()
    };
    
    await setDoc(doc(db, 'settings', 'app'), updatedSettings);
  } catch (error) {
    console.error('Erreur lors de la mise à jour du paramètre:', error);
    throw new Error('Impossible de mettre à jour le paramètre');
  }
};

/**
 * Réinitialiser les paramètres aux valeurs par défaut
 */
export const resetSettings = async (): Promise<void> => {
  try {
    await saveSettings(DEFAULT_SETTINGS);
  } catch (error) {
    console.error('Erreur lors de la réinitialisation des paramètres:', error);
    throw new Error('Impossible de réinitialiser les paramètres');
  }
}; 